<template>
  <div>
    <el-button @click="clearFilter"></el-button>
    <div class="el-search">
      <el-input placeholder="" v-model="value" class="input-with-select">
        <el-select class="selectWidth" v-model="type" slot="prepend" placeholder="">
          <el-option label="" value="name"></el-option>
          <el-option label="" value="addr"></el-option>
          <el-option label="" value="account"></el-option>
        </el-select>
        <el-button slot="append" icon="el-icon-search" @click="searchBtn"></el-button>
      </el-input>
    </div>
    <div>
      <el-table :data="petmasters" style="width: 100%" ref="filterTable">
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="phone" label=""></el-table-column>
        <el-table-column prop="addr" label=""></el-table-column>
        <el-table-column prop="account" label="" :filters="[{ text: '', value: '' }, { text: '', value: '' }]" :filter-method="filterAccount" filter-placement="bottom-end"></el-table-column>
        <el-table-column prop="" label="">
          <template slot-scope="scope">
            <el-button size="small" type="primary" @click="Unsealing( scope.row)"></el-button>
            <el-button size="small" type="danger" @click="Prohibition( scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="petmasterPagination.curpage" :page-sizes="[5, 10, 20, 30]" :page-size="petmasterPagination.eachpage" layout="total, sizes, prev, pager, next, jumper" :total="petmasterPagination.total"></el-pagination>
  </div>
</template>
<script>
import axios from "axios";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions, mapMutations } = createNamespacedHelpers(
  "platformModule"
);
export default {
  computed: {
    ...mapState(["petmasters", "petmasterPagination","petmasterSearch"])
  },
  data() {
    return {
      //
      value: "",
      type: "",
      currentSize: ""
    };
  },
  created() {
    this.setPetmasters();
  },
  methods: {
    ...mapActions(["setPetmasters"]),
    ...mapMutations(["setPetmasterSearch"]),
    //
    Unsealing(data) {
      let id = data._id;
      if (data.account == "") {
        return;
      } else {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            axios({
              method: "put",
              url: "/platform/putPetmaster/" + id,
              data: {
                account: ""
              }
            }).then(() => {
              this.setPetmasters();
              this.$message({
                type: "success",
                message: "!"
              });
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      }
    },
    //
    Prohibition(data) {
      let id = data._id;
      if (data.account == "") {
        return;
      } else {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            axios({
              method: "put",
              url: "/platform/putPetmaster/" + id,
              data: {
                account: ""
              }
            }).then(() => {
              this.setPetmasters();
              this.$message({
                type: "success",
                message: "!"
              });
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      }
    },
    //
    clearFilter() {
      this.$refs.filterTable.clearFilter();
    },
    //
    filterAccount(value, row) {
      return row.account === value;
    },
    //
    handleSizeChange(size) {
      this.setPetmasters({
        page: 1,
        rows: size,
        type: this.petmasterSearch.type,
        value: this.petmasterSearch.value
      });
      this.currentSize = size;
    },
    handleCurrentChange(page) {
      this.setPetmasters({
        page,
        rows: this.currentSize,
        type: this.petmasterSearch.type,
        value: this.petmasterSearch.value
      });
    },
    // 
    searchBtn() {
      let { type, value } = this;
      this.setPetmasterSearch({ type, value });
      this.setPetmasters({ page: 1, rows: 5, type, value });
    }
  }
};
</script>
<style scoped>
.select {
  width: 175px;
}
.selectP {
  text-align: center;
}
.el-select .el-input {
  width: 100px;
}
.selectWidth {
  width: 120px;
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
.el-search {
  margin-top: 15px;
  width: 500px;
}
</style>
