<template>
  <el-card class="box-card">
    <div slot="header" class="clearx">
      <span></span>
    </div>
    <el-form :model="regForm" status-icon :rules="rules" ref="regForm" label-width="100px">
      <el-form-item label="" prop="phone">
        <el-input v-model="regForm.phone" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="pwd">
        <el-input type="password" v-model="regForm.pwd" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="confrim">
        <el-input type="password" v-model="regForm.confrim"></el-input>
      </el-form-item>
      <el-form-item label="" prop="private">
        <el-radio label="" v-model="regForm.private" border></el-radio>
        <el-radio label="" v-model="regForm.private" border></el-radio>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="reg()"></el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      regForm: {
        private: "",
        phone: "",
        pwd: "",
        confirm: ""
      },
      //
      rules: {
        private: [{ required: true, message: "" }],
        phone: [
          { required: true, message: "" },
          { pattern: /^1\d{10}$/, message: "" },
          { validator: this.validatePhone }
        ],
        pwd: [
          { required: true, message: "" },
          { min: 6, message: "6" }
        ],
        confrim: [
          { required: true, message: "" },
          { validator: this.validatePwd }
        ]
      }
    };
  },
  methods: {
    validatePhone(rule, value, callback) {
      axios({
        method: "get",
        url: "/users",
        params: {
          phone: value
        }
      }).then(({ data }) => {
        if (data.status == 0) {
          callback("");
        } else {
          callback();
        }
      });
    },
    validatePwd(rule, value, callback) {
      if (value == this.regForm.pwd) {
        callback();
      } else {
        callback(new Error(""));
      }
    },
    reg() {
      this.$refs.regForm.validate(valid => {
        if (valid) {
          axios({
            method: "post",
            url: "/users",
            data: {
              phone: this.regForm.phone,
              password: this.regForm.pwd,
              private: this.regForm.private,
              status: "",
              account: ""
            }
          }).then(() => {
            this.$router.push("login");
          });
        } else {
          this.$alert("");
        }
      });
    }
  }
};
</script>

<style scoped>

#app>div {
  background: url("../../assets/1.jpg") no-repeat;
  background-size: cover;
}
.box-card {
  width: 500px;
  margin: 100px auto;
}
.clearx span {
  font-size: 30px;
}
</style>
