<template>
  <div>
    <el-table :data="producers" style="width: 100%">
      <el-table-column label="" prop="status"></el-table-column>
      <el-table-column prop="name" label="" width="120"></el-table-column>
      <el-table-column prop="desc" label="" width="120"></el-table-column>
      <el-table-column prop="corporate" label="" width="120"></el-table-column>
      <el-table-column prop="date" label="" width="120"></el-table-column>
      <el-table-column prop="office" label="" width="120"></el-table-column>
      <el-table-column prop="management" label="" width="120"></el-table-column>
      <el-table-column prop="capital" label="" width="120"></el-table-column>
      <el-table-column prop="No" label="" width="120"></el-table-column>
      <el-table-column prop="addr" label="" width="120"></el-table-column>
      <el-table-column prop="tel" label="" width="120"></el-table-column>
      <el-table-column prop="website" label="" width="120"></el-table-column>
      <el-table-column prop="license" label="" width="120">
        <template slot-scope="scope">
          <img :src="'/upload/'+scope.row.license" min-width="70" height="70">
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="showById(scope.row._id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <el-form :model="producer" status-icon ref="updateForm" label-width="100px">
        <!-- <span class="red">...</span> -->
        <el-form-item label="">
          <el-input v-model="producer.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="producer.desc" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="producer.corporate"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="producer.date"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="producer.office"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="producer.management"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="producer.capital"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="producer.No"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="producer.addr"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="producer.tel"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="producer.website"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-upload
            class="avatar-uploader"
            action="/upload"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload"
          >
            <img
              v-if="producer.license"
              :src="'/upload/'+producer.license"
              class="avatar"
            >
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="updateInformation"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import axios from "axios";

export default {
  data() {
    return {
      producers: [],
      producer: {},
      dialogVisible: false,
      isActive: true,
      hasError: false,
      centerDialogVisible: false,
      userId: ""
    };
  },

  created() {
    this.getuserId();
  },

  methods: {
    update() {
      (this.centerDialogVisible = false),
        (this.isActive = false),
        (this.hasError = true);
    },

    show() {
      axios({
        method: "get",
        url: "/supplier/info",
        params: {
          id: this.userId
        }
      }).then(({ data }) => {
        this.producers = data;
      });
    },
    getuserId() {
      axios({
        method: "get",
        url: "/users/getSession"
      }).then(({ data }) => {
        this.userId = data._id;
        this.show();
      });
    },
    showById(id) {
      this.dialogVisible = true;
      axios({
        method: "get",
        url: "/supplier/supplierInfo/" + id
      }).then(({ data }) => {
        this.producer = data;
      });
    },
    updateInformation() {
      this.dialogVisible = false;
      let {
        name,
        desc,
        corporate,
        date,
        office,
        management,
        capital,
        No,
        addr,
        tel,
        website,
        license
      } = this.producer;
      axios({
        method: "put",
        url: "/supplier/updateinfo/" + this.producer._id,
        data: {
          name,
          desc,
          corporate,
          date,
          office,
          management,
          capital,
          No,
          addr,
          tel,
          website,
          status: "",
          license
        }
      }).then(() => {
        this.show();
      });
    },
    handleAvatarSuccess(res) {
      this.producer.license = res;
    },
    beforeAvatarUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isLt2M;
    }
  }
};
</script>
<style scoped>
.avatar {
  width: 160px;
  height: 100px;
}
.red {
  color: tomato;
}
</style> 
