<template>
  <div>
    <el-table :data="products" style="width: 100%;textalign:center;height:80">
      <el-table-column prop="name" label="" width=100></el-table-column>
      <el-table-column prop="type" label=""></el-table-column>
      <el-table-column prop="makings" label="" width=160></el-table-column>
      <el-table-column prop="spec" label="" width=120></el-table-column>
      <el-table-column prop="suit" label=""></el-table-column>
      <el-table-column prop="weight" label="" width=160></el-table-column>
      <el-table-column prop="taste" label=""></el-table-column>
      <el-table-column prop="effect" label="" width=120></el-table-column>
      <el-table-column prop="addr" label=""></el-table-column>
      <el-table-column prop="pro_date" label="" width=120></el-table-column>
      <el-table-column prop="valid_date" label=""></el-table-column>
      <el-table-column prop="No" label="" width=120></el-table-column>
      <el-table-column prop="desc" label=""></el-table-column>
      <el-table-column prop="price" label=""></el-table-column>
      <!-- <el-table-column prop="img" label="" width=120></el-table-column> -->
       <el-table-column prop="number" label="" width=120></el-table-column>
      <el-table-column label="" width=200>
        <template slot-scope="scope">
          <el-button size="mini" @click="showById(scope.row._id)">/</el-button>
          <el-button size="mini" type="danger" @click="delProducts(scope.row._id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import { createNamespacedHelpers } from "vuex";
import axios from "axios"
const { mapState, mapMutations, mapActions } = createNamespacedHelpers(
  "productsMoudles"
);
export default {
  props: [],
  computed: {
    ...mapState(["products"])
  },
  methods: {
      ...mapMutations(["setupdateVisible"]),
      ...mapActions(["setProducts","setProduct","getSession"]),
    delProducts(id) {
      // console.log(id)
      axios({
        method: "delete",
        url: "/sopPropducts/" + id
      }).then(() => {
        // this.show();
          this.setProducts();
      });
    },
        showById(id){
         this.setupdateVisible(true)
         this.setProduct(id);
    }
  },
    created() {
    this.setProducts();
    this.getSession()
    // console.log(123);
  }
};
</script>

