<template>
  <div>
    <el-button type="primary" icon="el-icon-circle-plus-outline" @click="dialogVisible = true"></el-button>
    <el-button @click="clearFilter"></el-button>
    <div class="el-search">
      <el-input placeholder="" v-model="value" class="input-with-select">
        <el-select class="selectWidth" v-model="type" slot="prepend" placeholder="">
          <el-option label="" value="phone"></el-option>
          <el-option label="" value="private"></el-option>
          <el-option label="" value="status"></el-option>
          <el-option label="" value="account"></el-option>
        </el-select>
        <el-button slot="append" icon="el-icon-search" @click="searchBtn"></el-button>
      </el-input>
    </div>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <el-form :model="addForm" status-icon :rules="rules" ref="addForm" label-width="100px">
        <el-form-item label="" prop="private">
          <el-radio label="" v-model="addForm.private" border></el-radio>
          <el-radio label="" v-model="addForm.private" border></el-radio>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input type="text" v-model="addForm.phone" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="addForm.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirm">
          <el-input type="password" v-model="addForm.confirm" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="calcel"> </el-button>
        <el-button type="primary" @click="addAdministrators"> </el-button>
      </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="updateDialogVisible" width="30%">
      <el-form :model="user" status-icon ref="updateForm" :rules="rules" label-width="100px">
        <el-form-item label="" prop="password">
          <el-input v-model="password" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="updateCalcel"> </el-button>
        <el-button type="primary" @click="updateAdministrators"> </el-button>
      </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogDetailsVisible" width="30%">
      <div v-if="userPrivate == ''">
        <p v-if="shopOrsupplier.status == ''">
          <el-button type="success"></el-button>
        </p>
        <p v-if="shopOrsupplier.status == ''">
          <el-button type="info"></el-button>
        </p>
        <p>{{shopOrsupplier.username}}</p>
        <p>{{shopOrsupplier.addr}}</p>
        <!-- <p>{{shopOrsupplier.No}}</p> -->
        <p>{{shopOrsupplier.website}}</p>
        <p>

          <el-upload disabled="false" :show-file-list="false">
            <img v-if="shopOrsupplier.business_lic" :src="shopOrsupplier.business_lic" class="shoperImg">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </p>
        <p>{{shopOrsupplier.name}}</p>
        <p>{{shopOrsupplier.business_no}}</p>
        <p>{{shopOrsupplier.city}}</p>
        <p>{{shopOrsupplier.legal_person}}</p>
        <p>{{shopOrsupplier.tel}}</p>
        <p>
          <el-upload disabled="false" :show-file-list="false">
            <img v-if="shopOrsupplier.img_head" :src="shopOrsupplier.img_head" class="shoperImg">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </p>
        <p>{{shopOrsupplier.feature}}</p>
        <p>VIP{{shopOrsupplier.vip}}</p>
        <p>{{shopOrsupplier.commission_rate}}</p>
        <p v-if="shopOrsupplier.status == ''">
          <el-button type="primary" @click="agreeShopTwice(shopOrsupplier._id)"></el-button>
          <el-button type="danger" @click="refuseShopTwice(shopOrsupplier._id)"></el-button>
        </p>
      </div>
      <div v-if="userPrivate == ''">
        <p v-if="shopOrsupplier.status == ''">
          <el-button type="success"></el-button>
        </p>
        <p v-if="shopOrsupplier.status == ''">
          <el-button type="info"></el-button>
        </p>
        <p>{{shopOrsupplier.username}}</p>
        <p>{{shopOrsupplier.No}}</p>
        <p>{{shopOrsupplier.name}}</p>
        <p>{{shopOrsupplier.addr}}</p>
        <p>{{shopOrsupplier.tel}}</p>
        <p>{{shopOrsupplier.website}}</p>
        <p>
          <el-upload disabled="false" :show-file-list="false">
            <img v-if="shopOrsupplier.license" :src="shopOrsupplier.license" class="shoperImg">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </p>
        <p>{{shopOrsupplier.desc}}</p>
        <p v-if="shopOrsupplier.status == ''">
          <el-button type="primary" @click="agreeSupplierTwice(shopOrsupplier._id)"></el-button>
          <el-button type="danger" @click="refuseSupplierTwice(shopOrsupplier._id)"></el-button>
        </p>
      </div>
      <div v-if="shopOrsupplier.status == ''">
        <p></p>
      </div>
    </el-dialog>
    <div>
      <el-table :data="users" style="width: 100%" ref="filterTable">
        <el-table-column prop="phone" label=""></el-table-column>
        <el-table-column prop="password" label=""></el-table-column>
        <el-table-column prop="private" label="" :filters="[{ text: '', value: '' }, { text: '', value: '' }]" :filter-method="filterPrivate" filter-placement="bottom-end"></el-table-column>
        <el-table-column prop="status" label="" :filters="[{ text: '', value: '' }, { text: '', value: '' }, { text: '', value: '' }]" :filter-method="filterStatus" filter-placement="bottom-end"></el-table-column>
        <el-table-column prop="account" label="" :filters="[{ text: '', value: '' }, { text: '', value: '' }]" :filter-method="filterAccount" filter-placement="bottom-end"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="mini" @click="see(scope.row._id)"></el-button>
          </template>
        </el-table-column>
        <el-table-column prop="" width="400" label="" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" type="success" @click="agree(scope.row)">
              <i class="el-icon-check"></i></el-button>
            <el-button size="small" type="warning" @click="refuse(scope.row)">
              <i class="el-icon-minus"></i></el-button>
            <el-button size="small" type="danger" @click="prohibition(scope.row)">
              <i class="el-icon-close"></i></el-button>
            <el-button size="small" type="info" @click="unsealing(scope.row)">
              <i class="el-icon-view"></i></el-button>
            <el-button size="small" type="primary" @click="revise(scope.row._id)">
              <i class="el-icon-edit"></i></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pagination.curpage" :page-sizes="[5, 10, 20, 30]" :page-size="pagination.eachpage" layout="total, sizes, prev, pager, next, jumper" :total="pagination.total"></el-pagination>
  </div>
</template>
<script>
import axios from "axios";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions, mapMutations } = createNamespacedHelpers(
  "platformModule"
);
export default {
  computed: {
    ...mapState(["users", "user", "pagination", "search"]),
    password: {
      get() {
        return this.user.password;
      },
      set(password) {
        this.$store.commit("platformModule/setUser", {
          ...this.user,
          password
        });
      }
    }
  },
  created() {
    this.setUsers();
  },
  data() {
    return {
      //
      addForm: {
        private: "",
        phone: "",
        password: "",
        confirm: ""
      },
      shopOrsupplier: {},
      userPrivate: "",
      //
      rules: {
        private: [{ required: true, message: "" }],
        phone: [
          { required: true, message: "" },
          { pattern: /^1\d{10}$/, message: "" },
          { validator: this.validatePhone }
        ],
        password: [
          { required: true, message: "" },
          { min: 6, message: "" }
        ],
        confirm: [
          { required: true, message: "" },
          { validator: this.validatePwd }
        ]
      },
      dialogVisible: false,
      dialogDetailsVisible: false,
      updateDialogVisible: false,
      //
      currentSize: "",
      //
      value: "",
      type: ""
    };
  },
  methods: {
    ...mapActions(["setUsers", "setUser"]),
    ...mapMutations(["setSearch"]),
    //   
    validatePhone(rule, value, callback) {
      axios({
        method: "get",
        url: "/platform/findPhone",
        params: {
          phone: value
        }
      }).then(({ data }) => {
        if (data.status == 0) {
          callback("");
        } else {
          callback();
        }
      });
    },
    validatePwd(rule, value, callback) {
      if (value == this.addForm.password) {
        callback();
      } else {
        callback("");
      }
    },
    //
    see(id) {
      axios({
        method: "get",
        url: "/platform/findShopOrSupplier",
        params: {
          id
        }
      }).then(({ data }) => {
        // console.log("data", data.length);
        // console.log("private", data[0].user.private);
        this.dialogDetailsVisible = true;
        if (data.length == 0) {
          this.shopOrsupplier = {};
          this.userPrivate = "";
        } else {
          this.shopOrsupplier = data[0];
          this.userPrivate = data[0].user.private;
        }
      });
    },
    // 
    addAdministrators() {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          axios({
            method: "post",
            url: "/platform/reg",
            data: {
              phone: this.addForm.phone,
              password: this.addForm.password,
              private: this.addForm.private,
              status: "",
              account: ""
            }
          }).then(() => {
            this.dialogVisible = false;
            this.resetForm();
            this.setUsers();
          });
        } else {
          this.$alert("", "");
        }
      });
    },
    //
    calcel() {
      this.dialogVisible = false;
      this.resetForm();
    },
    //
    updateCalcel() {
      this.updateDialogVisible = false;
    },
    //
    clearFilter() {
      this.$refs.filterTable.clearFilter();
    },
    //
    filterPrivate(value, row) {
      return row.private === value;
    },
    //
    filterStatus(value, row) {
      return row.status === value;
    },
    //
    filterAccount(value, row) {
      return row.account === value;
    },
    //
    resetForm() {
      this.$refs.addForm.resetFields();
    },
    //
    agree(data) {
      let id = data._id;
      if (data.status != "") {
        return;
      } else {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            axios({
              method: "put",
              url: "/platform/putStatus/" + id,
              data: {
                status: ""
              }
            }).then(() => {
              this.setUsers();
              this.$message({
                type: "success",
                message: "!"
              });
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      }
    },
    //
    refuse(data) {
      let id = data._id;
      if (data.status != "") {
        return;
      } else {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            axios({
              method: "put",
              url: "/platform/putStatus/" + id,
              data: {
                status: ""
              }
            }).then(() => {
              this.setUsers();
              this.$message({
                type: "success",
                message: "!"
              });
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      }
    },
    //
    prohibition(data) {
      let id = data._id;
      if (data.account != "") {
        return;
      } else {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            axios({
              method: "put",
              url: "/platform/putStatus/" + id,
              data: {
                account: ""
              }
            }).then(() => {
              this.setUsers();
              this.$message({
                type: "success",
                message: "!"
              });
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      }
    },
    //
    unsealing(data) {
      let id = data._id;
      if (data.account != "") {
        return;
      } else {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            axios({
              method: "put",
              url: "/platform/putStatus/" + id,
              data: {
                account: ""
              }
            }).then(() => {
              this.setUsers();
              this.$message({
                type: "success",
                message: "!"
              });
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      }
    },
    //
    revise(id) {
      this.updateDialogVisible = true;
      this.setUser(id);
    },
    //
    updateAdministrators() {
      let { _id, password } = this.user;
      this.$refs.updateForm.validate(valid => {
        if (valid) {
          axios({
            method: "put",
            url: "/platform/putStatus/" + _id,
            data: {
              password
            }
          }).then(() => {
            this.updateDialogVisible = false;
            this.setUsers();
          });
        } else {
          this.$alert("", "");
        }
      });
    },
    //
    agreeShopTwice(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          axios({
            method: "put",
            url: "/platform/putShop/" + id,
            data: { status: "" }
          }).then(() => {
            this.dialogDetailsVisible = false;
            this.$message({
              type: "success",
              message: "!"
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    //
    refuseShopTwice(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          axios({
            method: "put",
            url: "/platform/putShop/" + id,
            data: { status: "" }
          }).then(() => {
            this.dialogDetailsVisible = false;
            this.$message({
              type: "success",
              message: "!"
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    //
    agreeSupplierTwice(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          axios({
            method: "put",
            url: "/platform/putSupplier/" + id,
            data: { status: "" }
          }).then(() => {
            this.dialogDetailsVisible = false;
            this.$message({
              type: "success",
              message: "!"
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    //
    refuseSupplierTwice(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          axios({
            method: "put",
            url: "/platform/putSupplier/" + id,
            data: { status: "" }
          }).then(() => {
            this.dialogDetailsVisible = false;
            this.$message({
              type: "success",
              message: "!"
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    //
    handleSizeChange(size) {
      this.setUsers({
        page: 1,
        rows: size,
        type: this.search.type,
        value: this.search.value
      });
      this.currentSize = size;
    },
    handleCurrentChange(page) {
      //   console.log(`: ${val}`);
      this.setUsers({
        page,
        rows: this.currentSize,
        type: this.search.type,
        value: this.search.value
      });
    },
    //
    searchBtn() {
      let { type, value } = this;
      this.setSearch({ type, value });
      this.setUsers({ page: 1, rows: 5, type, value });
    }
  }
};
</script>
<style scoped>
.select {
  width: 175px;
}
.selectP {
  text-align: center;
}
.el-select .el-input {
  width: 100px;
}
.selectWidth {
  width: 120px;
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
.el-search {
  margin-top: 15px;
  width: 500px;
}
.shoperImg {
  width: 300px;
  height: 200px;
}
</style>
