<template>
  <!--  -->
  <el-dialog title="" :visible.sync="closeDialogVisible" width="50%" :before-close="handleClose">
    <!--  -->
    <el-form
      :model="updateInfo"
      status-icon
      :rules="rules"
      ref="updateFrom"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="" prop="name">
        <el-input v-model="updateInfo.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-select v-model="updateInfo.type" placeholder="">
          <el-option label="" value="" name="type"></el-option>
          <el-option label="" value="" name="type"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker prop="day" v-model="updateInfo.startTime" type="datetime" placeholder=""></el-date-picker>
        <el-date-picker prop="day" v-model="updateInfo.endTime" type="datetime" placeholder=""></el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="time">
        <el-input-number v-model="updateInfo.time" :precision="2" :step="0.1" :max="10"></el-input-number>
      </el-form-item>
      <el-form-item label="">
        <el-switch v-model="updateInfo.delivery"></el-switch>
      </el-form-item>
      <el-form-item label="" prop="weight">
        <el-radio-group v-model="updateInfo.weight">
          <el-radio label="20kg" name="weight"></el-radio>
          <el-radio label="20-40kg" name="weight"></el-radio>
          <el-radio label="40-50kg" name="weight"></el-radio>
          <el-radio label="50kg" name="weight"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="serviceType">
        <el-radio-group v-model="updateInfo.serviceType">
          <el-radio label="" name="serviceType"></el-radio>
          <el-radio label="" name="serviceType"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="stuffLevel">
        <el-radio-group v-model="updateInfo.stuffLevel">
          <el-radio label="S" name="stuffLevel"></el-radio>
          <el-radio label="A" name="stuffLevel"></el-radio>
          <el-radio label="B" name="stuffLevel"></el-radio>
          <el-radio label="C" name="stuffLevel"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="price">
        <el-input v-model="updateInfo.price"></el-input>
      </el-form-item>
      <el-form-item label="" prop="text">
        <el-input type="textarea" v-model="updateInfo.text"></el-input>
      </el-form-item>
    </el-form>

    <span slot="footer" class="dialog-footer">
      <el-button @click="handleCancle"> </el-button>
      <el-button type="primary" @click="handleSubmit('updateFrom')"> </el-button>
    </span>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions, mapMutations } = createNamespacedHelpers(
  "serviceModules"
);
export default {
  data() {
    return {
      rules: {
        name: [{ required: true, message: "" }],
        type: [{ required: true, message: "" }],
        day: [{ required: true, message: "" }],
        text: [{ required: true, message: "" }],
        time: [{ required: true, message: "" }],
        price: [{ required: true, message: "" }]
      }
    };
  },
  computed: {
    ...mapState(["updateInfo", "dialogVisible"]),
    closeDialogVisible: {
      get() {
        return this.dialogVisible;
      },
      set() {
        this.setUpdateVisible(false);
      }
    }
  },
  methods: {
    ...mapMutations(["setUpdateVisible", "setServiceInfo"]),
    ...mapActions(["getServices"]),
    handleClose(done) {
      this.$confirm("")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    handleCancle() {
      this.setUpdateVisible(false);
    },
    handleSubmit(updateFrom) {
      //   console.log(this.updateInfo.name)
      this.$refs[updateFrom].validate(valid => {
        if (valid) {
          axios({
            method: "put",
            url: "/services/" + this.updateInfo._id,
            data: {
              name: this.updateInfo.name,
              type: this.updateInfo.type,
              time: this.updateInfo.time,
              startTime: this.updateInfo.startTime,
              endTime: this.updateInfo.endTime,
              delivery: this.updateInfo.delivery,
              weight: this.updateInfo.weight,
              serviceType: this.updateInfo.serviceType,
              stuffLevel: this.updateInfo.stuffLevel,
              price: this.updateInfo.price,
              text: this.updateInfo.text
            }
          }).then(() => {
            this.getServices();
            this.setUpdateVisible(false);
          });
        } else {
          alert("");
          return false;
        }
      });
    }
  }
};
</script>

<style scoped>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>
