<template>
  <div>
    <el-button type="primary" icon="el-icon-edit" @click="dialogVisible = true"></el-button>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
      <!--  -->
      <el-form
        :model="addForm"
        status-icon
        :rules="rules"
        ref="addForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="addForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="addForm.type" placeholder="">
            <el-option label="" value="" name="type"></el-option>
            <el-option label="" value="" name="type"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="day">
          <el-date-picker
            prop="day"
            v-model="addForm.startTime"
            type="datetime"
            placeholder=""
          ></el-date-picker>
          <el-date-picker
            prop="day"
            v-model="addForm.endTime"
            type="datetime"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="time">
          <el-input-number v-model="addForm.time" :precision="2" :step="0.1" :max="10"></el-input-number>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="addForm.delivery"></el-switch>
        </el-form-item>
        <el-form-item label="" prop="weight">
          <el-radio-group v-model="addForm.weight">
            <el-radio label="20kg" name="weight"></el-radio>
            <el-radio label="20-40kg" name="weight"></el-radio>
            <el-radio label="40-50kg" name="weight"></el-radio>
            <el-radio label="50kg" name="weight"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="serviceType">
          <el-radio-group v-model="addForm.serviceType">
            <el-radio label="" name="serviceType"></el-radio>
            <el-radio label="" name="serviceType"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="stuffLevel">
          <el-radio-group v-model="addForm.stuffLevel">
            <el-radio label="S" name="stuffLevel"></el-radio>
            <el-radio label="A" name="stuffLevel"></el-radio>
            <el-radio label="B" name="stuffLevel"></el-radio>
            <el-radio label="C" name="stuffLevel"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="price">
          <el-input v-model="addForm.price"></el-input>
        </el-form-item>
        <el-form-item label="" prop="text">
          <el-input type="textarea" v-model="addForm.text"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="cancleBtn"> </el-button>
        <el-button type="primary" @click="addBtn('addForm')"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("serviceModules");
export default {
  data() {
    return {
      day: "",
      dialogVisible: false,
      addForm: {
        name: "",
        type: "",
        startTime: "",
        endTime: "",
        time: "",
        delivery: "",
        weight: [],
        serviceType: "",
        stuffLevel: "",
        price: "",
        text: ""
      },
      rules: {
        name: [{ required: true, message: "" }],
        type: [{ required: true, message: "" }],
        startTime: [{ required: true, message: "" }],
        endTime: [{ required: true, message: "" }],
        text: [{ required: true, message: "" }],
        time: [{ required: true, message: "" }],
        price: [{ required: true, message: "" }]
      }
    };
  },
  computed: {
    ...mapState(["shopId"])
  },
  methods: {
    ...mapActions(["getServices"]),
    // ...mapMutations(["setStuInfo"]),
    handleClose(done) {
      this.$confirm("")
        .then(() => {
          done();
          this.$refs.addForm.resetFields();
        })
        .catch(() => {});
    },
    cancleBtn: function() {
      this.dialogVisible = false;
      this.$refs.addForm.resetFields();
    },
    addBtn(addForm) {
      this.$refs[addForm].validate(valid => {
        if (valid) {
          axios({
            method: "post",
            url: "/services",
            data: {
              name: this.addForm.name,
              type: this.addForm.type,
              startTime: this.addForm.startTime,
              endTime: this.addForm.endTime,
              time: this.addForm.time,
              delivery: this.addForm.delivery,
              weight: this.addForm.weight,
              serviceType: this.addForm.serviceType,
              stuffLevel: this.addForm.stuffLevel,
              price: this.addForm.price,
              text: this.addForm.text,
              id: this.shopId
            }
          }).then(() => {
            this.dialogVisible = false;
            this.$refs.addForm.resetFields();
            this.getServices();
          });
        } else {
          alert("");
          return false;
        }
      });
    }
  }
};
</script>

<style>
</style>
