<template>
  <span>
    <el-button type="danger" @click="dialogVisible = true"></el-button>
    <el-dialog title="" :visible.sync="dialogVisible" width="100%">
      <el-table :data="supProducts" style="width: 100%;textalign:center;height:80">
        <el-table-column prop="name" label="" width="100"></el-table-column>
        <el-table-column prop="type" label=""></el-table-column>
        <el-table-column prop="makings" label="" width="160"></el-table-column>
        <el-table-column prop="spec" label="" width="120"></el-table-column>
        <el-table-column prop="suit" label=""></el-table-column>
        <el-table-column prop="weight" label="" width="160"></el-table-column>
        <el-table-column prop="taste" label=""></el-table-column>
        <el-table-column prop="effect" label="" width="120"></el-table-column>
        <el-table-column prop="addr" label=""></el-table-column>
        <el-table-column prop="pro_date" label="" width="120"></el-table-column>
        <el-table-column prop="valid_date" label=""></el-table-column>
        <el-table-column prop="No" label="" width="120"></el-table-column>
        <el-table-column prop="desc" label=""></el-table-column>
        <el-table-column prop="price" label=""></el-table-column>
        <el-table-column prop="inventory" label="" width="120"></el-table-column>
        <el-table-column label="" width="200">
          <template slot-scope="scope">
            <el-button size="middle" @click="postSupPro(scope.row._id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-button @click="dialogVisible = false"> </el-button>
    </el-dialog>
  </span>
</template>
<script>
import { createNamespacedHelpers } from "vuex";
import axios from "axios";
const { mapState, mapActions } = createNamespacedHelpers(
  "productsMoudles"
);
export default {
  data() {
    return {
      dialogVisible: false,
      number: 1
    };
  },
  props: [],
  computed: {
    ...mapState(["supProducts", "shopId"])
  },
  methods: {
    handleChange() {
      // console.log(value);
    },
    ...mapActions(["setSupProducts", "setProducts"]),
    postSupPro(id) {
      axios({
        method: "get",
        url: "/supPro/" + id
      }).then(({ data }) => {
        // console.log("gongyingshang", data);
        axios({
          method: "post",
          url: "/sopPropducts",
          data: {
            data,
            number: this.number,
            shopId: this.shopId
          }
        }).then(() => {
          // console.log("", ...data);
          this.$message({
            showClose: true,
            message: "",
            type: "success"
          });
          this.setProducts();
        });
      });
    }
  },
  created() {
    // console.log(1234);
    this.setSupProducts();
  }
};
</script>

