
/* eslint-disable */
<template>
  <el-dialog title="" :visible.sync="closeDialogVisible" width="30%">
    <!--  -->
    <el-form
      :model="updateInfo"
      status-icon
      :rules="rules"
      ref="updateFrom"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="" prop="state">
          <el-input v-if="updateInfo.state == 0" type="text" value="" autocomplete="off" :disabled="true"></el-input>
          <el-input v-else-if="updateInfo.state == 1" type="text" value="" autocomplete="off" :disabled="true"></el-input>
          <el-input v-else-if="updateInfo.state == 2" type="text" value="" autocomplete="off" :disabled="true"></el-input>
          <el-input v-else-if="updateInfo.state == 3" type="text" value="" autocomplete="off" :disabled="true"></el-input>
          <el-input v-else-if="updateInfo.state == 4" type="text" value="" autocomplete="off" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="" prop="user">
        <el-input type="text" v-model="updateInfo.user" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="addr">
        <el-input type="text" v-model="updateInfo.addr" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="updateInfo.phone"></el-input>
      </el-form-item>
      <el-form-item label="" prop="price">
        <el-input v-model="updateInfo.price"></el-input>
      </el-form-item>
    </el-form>

    <span slot="footer" class="dialog-footer">
      <el-button @click="handleCancle"> </el-button>
      <el-button type="primary" @click="handleSubmit('updateFrom')"> </el-button>
    </span>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions, mapMutations } = createNamespacedHelpers(
  "orderModules"
);
export default {
  data() {
    return {
      rules: {
        user: [{ required: true, message: "" }],
        addr: [{ required: true, message: "" }],
        price: [{ required: true, message: "" }],
        phone: [{ required: true, message: "" }]
      }
    };
  },
  computed: {
    ...mapState(["updateInfo", "dialogVisible"]),
    closeDialogVisible: {
      get() {
        return this.dialogVisible;
      },
      set() {
        this.setUpdateVisible(false);
      }
    }
  },
  methods: {
    ...mapMutations(["setUpdateVisible", "setOrderInfo"]),
    ...mapActions(["getOrders"]),
    handleCancle() {
      this.setUpdateVisible(false);
    },
    handleSubmit(updateFrom) {
      // console.log(this.updateInfo.user)
      this.$refs[updateFrom].validate(valid => {
        if (valid) {
          axios({
            method: "put",
            url: "/orders/" + this.updateInfo._id,
            data: {
              user: this.updateInfo.user,
              addr: this.updateInfo.addr,
              price: this.updateInfo.price,
              phone: this.updateInfo.phone
            }
          }).then(() => {
            this.getOrders();
            this.setUpdateVisible(false);
          });
        } else {
          alert("");
          return false;
        }
      });
    }
  }
};
</script>

<style scoped>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>
