<template>
  <div>
    <el-button type="primary" icon="el-icon-circle-plus-outline" @click="openAddDialog"></el-button>
    <el-button @click="clearFilter"></el-button>
    <div class="el-search">
      <el-input placeholder="" v-model="value" class="input-with-select">
        <el-select class="selectWidth" v-model="type" slot="prepend" placeholder="">
          <el-option label="" value="username"></el-option>
          <el-option label="" value="name"></el-option>
          <el-option label="" value="city"></el-option>
          <el-option label="" value="legal_person"></el-option>
          <el-option label="" value="status"></el-option>
          <el-option label="" value="account"></el-option>
        </el-select>
        <el-button slot="append" icon="el-icon-search" @click="searchBtn"></el-button>
      </el-input>
    </div>
    <div>
      <el-dialog title="" :visible.sync="addDialogVisible" width="50%">
        <el-form :model="addShopForm" :rules="rules" status-icon ref="addShopForm" label-width="150px">
          <el-form-item label="" prop="id">
            <el-select v-model="addShopForm.id" placeholder="">
              <el-option v-for="item in regShoper" :key="item" :label="item.phone" :value="item._id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="username" label="">
            <el-input v-model="addShopForm.username" type="text" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item prop="name" label="">
            <el-input v-model="addShopForm.name" type="text" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="img_head">
            <el-upload class="avatar-uploader" action="/upload" :show-file-list="false" :on-success="handleAvatarSuccessShop" :before-upload="beforeAvatarUploadShop">
              <img v-if="addShopForm.img_head" :src="addShopForm.img_head" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item prop="business_no" label="">
            <el-input v-model="addShopForm.business_no" type="text" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="business_lic">
            <el-upload class="avatar-uploader" action="/upload" :show-file-list="false" :on-success="handleAvatarSuccessBus" :before-upload="beforeAvatarUploadBus">
              <img v-if="addShopForm.business_lic" :src="addShopForm.business_lic" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item prop="legal_person" label="">
            <el-input v-model="addShopForm.legal_person" type="text" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="addr">
            <el-input v-model="addShopForm.addr" @blur="getLocation" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=":">
            <el-input v-model="addShopForm.location" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="city">
            <el-input v-model="addShopForm.city" type="text" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="tel">
            <el-input v-model="addShopForm.tel" type="text" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="feature">
            <el-input v-model="addShopForm.feature" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="website">
            <el-input v-model="addShopForm.website" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="VIP:" prop="vip">
            <el-input v-model="addShopForm.vip" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=":" prop="commission_rate">
            <el-input v-model="addShopForm.commission_rate" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="calcle"> </el-button>
          <el-button type="primary" @click="addStore"> </el-button>
        </span>
      </el-dialog>
      <UptateStore></UptateStore>
      <el-table :data="stores" style="width: 100%" ref="filterTable">
        <el-table-column width="120" prop="username" label=""></el-table-column>
        <el-table-column width="120" prop="name" label="" show-overflow-tooltip></el-table-column>
        <el-table-column width="120" prop="addr" label="" show-overflow-tooltip></el-table-column>
        <el-table-column width="120" prop="business_no" label="" show-overflow-tooltip></el-table-column>
        <el-table-column width="120" prop="business_lic" label="" show-overflow-tooltip></el-table-column>
        <el-table-column width="120" prop="city" label="" show-overflow-tooltip></el-table-column>
        <el-table-column width="120" prop="location" label="" show-overflow-tooltip></el-table-column>
        <el-table-column width="120" prop="legal_person" label=""></el-table-column>
        <el-table-column width="120" prop="tel" label=""></el-table-column>
        <el-table-column width="120" prop="img_head" label="" show-overflow-tooltip></el-table-column>
        <el-table-column width="120" prop="feature" label="" show-overflow-tooltip></el-table-column>
        <el-table-column width="120" prop="website" label="" show-overflow-tooltip></el-table-column>
        <el-table-column width="120" prop="vip" label="VIP"></el-table-column>
        <el-table-column width="120" prop="commission_rate" label=""></el-table-column>
        <el-table-column width="120" prop="status" label="" :filters="[{ text: '', value: '' }, { text: '', value: '' }, { text: '', value: '' }]" :filter-method="filterStatus" filter-placement="bottom-end"></el-table-column>
        <el-table-column width="120" prop="account" label="" :filters="[{ text: '', value: '' }, { text: '', value: '' }]" :filter-method="filterAccount" filter-placement="bottom-end"></el-table-column>
        <el-table-column width="250" fixed="right" label="">
          <template slot-scope="scope">
            <el-button size="small" type="primary" @click="updateShop(scope.row._id)">
              <i class="el-icon-edit"></i></el-button>
            <el-button size="small" type="success" @click="unsealingShop(scope.row)">
              <i class="el-icon-check"></i></el-button>
            <el-button size="small" type="danger" @click="prohibitionShop(scope.row)">
              <i class="el-icon-close"></i></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="shopPagination.curpage" :page-sizes="[5, 10, 20, 30]" :page-size="shopPagination.eachpage" layout="total, sizes, prev, pager, next, jumper" :total="shopPagination.total"></el-pagination>
  </div>
</template>
<script>
import UptateStore from "./updateStore.vue";
import axios from "axios";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions, mapMutations } = createNamespacedHelpers(
  "platformModule"
);
export default {
  components: {
    UptateStore
  },
  computed: {
    ...mapState(["stores", "shopPagination", "shopSearch"])
  },
  data() {
    return {
      addDialogVisible: false,
      //shop
      regShoper: [],
      //
      value: "",
      type: "",
      currentSize: "",
      //
      addShopForm: {
        id: "",
        username: "",
        name: "",
        img_head: "",
        business_no: "",
        business_lic: "",
        legal_person: "",
        addr: "",
        location: "",
        city: "",
        tel: "",
        feature: "",
        website: "",
        vip: "",
        commission_rate: "",
        stuff: []
      },
      // 
      rules: {
        id: [{ required: true, message: "", trigger: "blur" }],
        username: [{ required: true, message: "" }],
        name: [{ required: true, message: "" }],
        business_no: [{ required: true, message: "" }],
        addr: [
          { required: true, message: "", trigger: "blur" }
        ],
        city: [{ required: true, message: "" }],
        legal_person: [{ required: true, message: "" }],
        feature: [{ required: true, message: "" }],
        website: [{ required: true, message: "" }],
        vip: [{ required: true, message: "vip" }],
        commission_rate: [{ required: true, message: "" }],
        tel: [
          { required: true, message: "" },
          {
            pattern: /^[1]{1}[3,5,7,8,9]{1}[0-9]{9}$/,
            message: ""
          }
        ],
        business_lic: [
          { required: true, message: "", trigger: "blur" }
        ],
        img_head: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    this.setStores();
  },
  methods: {
    ...mapActions(["setStores", "setStore"]),
    ...mapMutations(["setUpdateShopVisible", "setShopSearch"]),
    //
    openAddDialog() {
      this.addDialogVisible = true;
      this.findUserForShop();
    },
    // shop
    unsealingShop(data) {
      let id = data._id;
      if (data.account == "") {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            axios({
              method: "put",
              url: "/platform/putShop/" + id,
              data: { account: "" }
            }).then(() => {
              this.setStores();
              this.$message({
                type: "success",
                message: "!"
              });
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      } else {
        return;
      }
    },
    //
    prohibitionShop(data) {
      let id = data._id;
      if (data.account == "") {
        return;
      } else {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            axios({
              method: "put",
              url: "/platform/putShop/" + id,
              data: { account: "" }
            }).then(() => {
              this.setStores();
              this.$message({
                type: "success",
                message: "!"
              });
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      }
    },
    //
    calcle() {
      this.addDialogVisible = false;
      this.resetForm();
      this.addShopForm.location = "";
    },
    //
    resetForm() {
      this.$refs.addShopForm.resetFields();
    },
    //usershop
    findUserForShop() {
      axios({
        method: "get",
        url: "/platform/findUserForShop"
      }).then(({ data }) => {
        this.regShoper = data;
      });
    },
    //
    getLocation() {
      axios({
        method: "get",
        url: "/platform/addr",
        params: {
          addr: this.addShopForm.addr
        }
      }).then(({ data }) => {
        let lng = `${data.result.location.lng}`;
        let lat = `${data.result.location.lat}`;
        this.addShopForm.location = lng + "," + lat;
      });
    },
    //
    //
    handleAvatarSuccessShop(res) {
      this.addShopForm.img_head = "/upload/" + res;
    },
    beforeAvatarUploadShop(file) {
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.$message.error(" 2MB!");
        return isLt2M;
      }
    },
    //
    handleAvatarSuccessBus(res) {
      this.addShopForm.business_lic = "/upload/" + res;
    },
    beforeAvatarUploadBus(file) {
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.$message.error(" 2MB!");
        return isLt2M;
      }
    },
    //
    addStore() {
      this.$refs.addShopForm.validate(valid => {
        if (valid) {
          axios({
            method: "post",
            url: "/platform/addShop",
            data: {
              id: this.addShopForm.id,
              username: this.addShopForm.username,
              name: this.addShopForm.name,
              img_head: this.addShopForm.img_head,
              business_no: this.addShopForm.business_no,
              business_lic: this.addShopForm.business_lic,
              legal_person: this.addShopForm.legal_person,
              addr: this.addShopForm.addr,
              location: this.addShopForm.location,
              city: this.addShopForm.city,
              tel: this.addShopForm.tel,
              feature: this.addShopForm.feature,
              website: this.addShopForm.website,
              vip: this.addShopForm.vip,
              commission_rate: this.addShopForm.commission_rate,
              stuff: this.addShopForm.stuff,
              status: "",
              account: ""
            }
          }).then(() => {
            this.setStores();
            this.resetForm();
            this.addDialogVisible = false;
            this.addShopForm.location = "";
          });
        } else {
          this.$alert("", "");
        }
      });
    },
    //
    updateShop(id) {
      this.setStore(id);
      this.setUpdateShopVisible(true);
    },
    //
    clearFilter() {
      this.$refs.filterTable.clearFilter();
    },
    //
    filterStatus(value, row) {
      return row.status === value;
    },
    //
    filterAccount(value, row) {
      return row.account === value;
    },
    //
    handleSizeChange(size) {
      this.setStores({
        page: 1,
        rows: size,
        type: this.shopSearch.type,
        value: this.shopSearch.value
      });
      this.currentSize = size;
    },
    handleCurrentChange(page) {
      //   console.log(`: ${val}`);
      this.setStores({
        page,
        rows: this.currentSize,
        type: this.shopSearch.type,
        value: this.shopSearch.value
      });
    },
    // 
    searchBtn() {
      let { type, value } = this;
      this.setShopSearch({ type, value });
      this.setStores({ page: 1, rows: 5, type, value });
    }
  }
};
</script>
<style scoped>
.avatar-uploader,
.el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  width: 300px;
  position: relative;
  overflow: hidden;
  text-align: center;
}
.avatar-uploader,
.el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}

.select {
  width: 175px;
}
.selectP {
  text-align: center;
}
.el-select .el-input {
  width: 100px;
}
.selectWidth {
  width: 120px;
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
.el-search {
  margin-top: 15px;
  width: 500px;
}
</style>
